import React, { Component } from 'react';
import Medal from './Medal';

import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';


class Country extends Component {

    
    
    findTotal = (country, medals) => {
      let totalMedals = 0; 
      medals.forEach(medal => {
        totalMedals += country[medal]
      })
      return totalMedals;

    }
    
    render() { 
      const {onIncrement, country, medals} = this.props;
      return (
        <Card variant="outlined" sx={{backgroundColor:"#414344",  marginLeft:"2%", marginRight:"2%"}}>
          <CardContent>
            <Typography color="white">
              <Typography  variant="h3" component="div">
                {country.name}
              </Typography>
              <Typography variant="h5">
                Total Medals: {this.findTotal(country, medals)}
              </Typography>
              
              {medals.map(medal => 
                  <Medal 
                    key= {medal}
                    medal = {medal}
                    country = {country}
                    onIncrement= {onIncrement}/>
                  )}
              
            </Typography>
            
          </CardContent>

        </Card>
        
        // <div style={{backgroundColor:"#414344", paddingBottom:"5px", marginLeft:"30%", marginRight:"30%"}}>
        //     <div>
        //       <h3 style={{paddingTop:"5px"}}>
        //         {country.name}
        //       </h3>
        //       <div style={{fontWeight:"bold", marginBottom:"5px"}}>
        //         Total Medals: {this.findTotal(country, medals)}
        //       </div>
        //     </div>
        //       {medals.map(medal => 
        //         <Medal 
        //           key= {medal}
        //           medal = {medal}
        //           country = {country}
        //           onIncrement= {onIncrement}/>
        //         )}

                          
        // </div>
      );
    }
  }
  
  export default Country