import { IconButton } from "@mui/material";
import ControlPointOutlinedIcon from '@mui/icons-material/ControlPoint';
import RemoveCircleOutlineOutlinedIcon from '@mui/icons-material/RemoveCircleOutlineOutlined';
import { Component } from "react";
import {Grid }from "@mui/material";

class Medal extends Component {
    uppercaseMedal = (medal) => {
        return medal[0].toUpperCase() + medal.slice(1);;
    }
    render() {
        const {onIncrement, country, medal} = this.props;
        return (
            
                <Grid container style={{display: "flex", justifyContent: "center",alignItems:"center"}}>
                    <Grid xs={5} style={{display:"flex", justifyContent:"right", alignItems:"center"}}>
                        <span>{this.uppercaseMedal(medal)} Medal Count: {country[medal]}</span>
                    </Grid>
                    <Grid xs={3} style={{display:"flex", justifyContent:"left", alignItems:"center"}}>
                        <IconButton aria-label="controlPointOutlined" color="success" onClick={() => onIncrement(country.id, 1, medal) }  style={{ marginLeft: "5px", width:'25px'}}>
                            <ControlPointOutlinedIcon />
                        </IconButton>
                        {country[medal] > 0 ? 
                        <IconButton color="error" onClick={() => onIncrement(country.id, -1 , medal) } style={{ marginLeft: "5px", width:'25px'}}>
                            <RemoveCircleOutlineOutlinedIcon />
                        </IconButton> :
                        <IconButton disabled style={{ marginLeft: "5px", width:'25px'}}>
                            <RemoveCircleOutlineOutlinedIcon />
                        </IconButton>
                        }                            
                
                    </Grid>

                </Grid>
                
            
                
            
        );
    }

}
export default Medal