import React, { Component } from 'react';
import './App.css';
import { Grid } from '@mui/material';
import Country from './components/Country';

class App extends Component {
  state = {
    countries: [
      { id:1, name:'United States', gold:2, silver: 2, bronze: 1},
      { id:2, name:'Mexico', gold:3, silver:1, bronze: 0},
      { id:3, name:'Germany', gold:0, silver:4, bronze: 3},
    ],
    medals: ['gold', 'silver','bronze']
  }
  findTotalMedals = () => {
    let totalMedals = 0;
    this.state.countries.forEach(country => {
      this.state.medals.forEach(medal => {
        totalMedals += country[medal];
      })
    })
    return totalMedals;

  }
  handleIncrement = (id, incrementAmount, medal) => {
    const mutableCountries = [...this.state.countries];
    let idx = mutableCountries.findIndex( c => c.id === id);
    mutableCountries[idx][medal] += incrementAmount;
    this.setState({ contries:mutableCountries }); 
  }
  render() { 
    return ( 
      <div className="App">
        <h1>Olympic Medals: {this.findTotalMedals()} </h1>
        <Grid container>
        {this.state.countries.map(country =>
          <Grid item xs={4}>
            <Country 
              key={country.id}
              country={country}
              medals = {this.state.medals}
              onIncrement={this.handleIncrement}/>
          </Grid>
        )}
        </Grid>
        
      </div>
     );
  }
}
 
export default App;